/* Swiper css */
.swiper-wrapper {width:462px !important; margin:0 auto;}

.swiper-slide img {width:147px;}


.swiper-button-next{
  right:0 !important;
  top:110px !important;
}


.swiper-button-next:after {
  color:#eee !important;
  font-size:32px !important;
}

.swiper-button-next:hover:after {
  color:#000 !important;
}


.swiper-button-prev{
  left:0 !important;
  top:110px !important;
}

.swiper-button-prev:after {
  color:#eee !important;
  font-size:32px !important;
}
.swiper-button-prev:hover:after{
  color:#000 !important;
}

